@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700|Oswald:400,700|Roboto+Condensed:400,700&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  background-image: url(/react-pogoda/static/media/nature.272ecf33.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.weather {
  text-align: center;
  color: white
}

.weather h1 {
  width: 80%;
  height: 86px;
  margin-top: 30px;
  margin-bottom: 30px;
  margin-left: 10%;
  font-size: 46px;
  line-height: 86px;
  text-transform: uppercase;
  letter-spacing: 30px;
  word-spacing: 50px;
  box-shadow: 0 0 10px 1px white;
  background-color: rgba(0, 0, 0, .4);
}
/* font-family: 'Oswald', sans-serif;
font-family: 'Roboto Condensed', sans-serif;
font-family: 'Open Sans', sans-serif; */

form input {
  margin: 0px 15px 15px 15px;
  padding: 10px 15px;
  height: 40px;
  font-size: 20px;
  background-color: rgba(0, 0, 0, .5);
  color: white;
}

form input:focus {
  background-color: #fff;
  color: black;
  transition: .3s
}

form input:focus::-webkit-input-placeholder {
  color: black
}

form input:focus::-ms-input-placeholder {
  color: black
}

form input:focus::placeholder {
  color: black
}

input::-webkit-input-placeholder {
  color: #ddd;
  font-size: 16px;
}

input::-ms-input-placeholder {
  color: #ddd;
  font-size: 16px;
}

input::placeholder {
  color: #ddd;
  font-size: 16px;
}

form button {
  margin: 0px 15px 15px 15px;
  padding: 10px 15px;
  font-size: 20px;
  background-color: rgba(0, 0, 0, .5);
  color: white;
  border: 1px solid black;
  cursor: pointer;
  outline: none;
}

form button:hover {
  background-color: black;
  color: white;
  border-color: white;
  transition: .4s;

}
/* font-family: 'Oswald', sans-serif;
font-family: 'Roboto Condensed', sans-serif;
font-family: 'Open Sans', sans-serif; */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


div.wrap {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 25%;
  bottom: 5%;
  left: 25%;
  right: 25%;
  font-family: 'Oswald', sans-serif;
  background-color: rgba(10, 60, 157, .8);
  box-shadow: 0 0 10px 2px white;
  z-index: -1
}

.cityName {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-basis: 8%;
  margin: 0 10px;
  border-bottom: 2px solid #ccc;
  opacity: .8;
}

.cityName p {
  font-size: 26px;
  margin-left: 30px;
  font-weight: bold;
  letter-spacing: 2px;
}

.cityName .wind {
  display: flex;
  align-items: center;
  margin-right: 30px;
}

.cityName .wind span {
  font-size: 26px;
  font-weight: bold;
}

.cityName svg {
  margin-right: 20px;
  font-size: 36px;
}


/* ---------------.mainWeather .right----------------------------------------------- */
.mainWeather {
  display: flex;
  flex-basis: 54%;
  margin: 0 10px;
  border-bottom: 2px solid rgba(204, 204, 204, .8);
}

.mainWeather .left {
  width: 60%;
  height: 100%;
}


.mainWeather .right {
  width: 40%;
  height: 100%;
}

.mainWeather .right div.icon {
  position: relative;
  height: 90%;
}

.mainWeather .right div.icon img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%
}

.mainWeather .right div.icon p {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  font-size: 40px;
  padding: 5px;
  z-index: 1;

}

.mainWeather .right .description {
  height: 10%;
  font-size: 20px;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  opacity: .8;

}


/* ---------------.mainWeather .left----------------------------------------------- */
.mainWeather .left .date {
  position: relative;
  height: 25%;
}

.mainWeather .left .date p {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  text-align: left;
  margin-left: 30px;
  font-family: 'Oswald', sans-serif;
  font-size: 32px;
  word-spacing: 5px;
  letter-spacing: 2px;
  opacity: .8;
}

.mainWeather .left .time {
  position: relative;
  height: 50%;

}

.mainWeather .left .time p {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  font-size: 120px;
  font-weight: bold;
  text-align: left;
  margin-left: 30px;
  font-family: 'Roboto Condensed', sans-serif;
}


.mainWeather .left .parameters {
  height: 25%;
  text-align: left;
  margin-left: 30px;
  opacity: .8;
}

.mainWeather .left .parameters span.humidity,
.mainWeather .left .parameters span.hPa {
  display: inline-block;
  margin-right: 30px;
  height: 50%;
  font-size: 24px;
}

.mainWeather .left .parameters span.humidity svg,
.mainWeather .left .parameters span.hPa svg {
  margin: 0 10px 0 0px;
}

.mainWeather .left .parameters p.sun {
  font-size: 24px;
}

.mainWeather .left .parameters p.sun span.sunrise {
  margin-right: 30px;
}

