/* ----------------------------.update-------------------------------------- */


.update {
  background-color: blue;
  opacity: .5;
  flex-basis: 8%
}
.longWeather {

  flex-basis: 30%;
  display: flex;
  justify-content: space-around;
  margin: 0 10px;
  border-bottom: 2px solid rgba(204, 204, 204, .8);
  /* background-color: green; */
}

.longWeather .content {
  flex-basis: 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  font-family: 'Oswald', sans-serif;
  /* font-family: 'Open Sans', sans-serif; */
  /* font-family: 'Roboto Condensed', sans-serif; */
  font-size: 20px;
  /* letter-spacing: 1px; */
  /* opacity: .8; */
  /* background-color: gray; */
}

.longWeather .day {
  margin-top: 5px;
  letter-spacing: 1px;
  /* opacity: .8; */
}

.longWeather .smallIcon {
  position: relative;
  height: 70%;
}

.longWeather .smallIcon img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 120%;
  max-height: 120%
}

.longWeather .temp {
  margin-bottom: 5px;
  /* opacity: .8; */
}
